import {
  stringColumnPreset,
  AppGridConfig,
  boolColumnPreset,
  routerLinkIdPreset,
  currencyColumnPresetNoEdit,
  severityColumnPreset,
  SeverityItem,
} from '@/components/grid';
import {ModerOrderDisplayItem} from '@/services/order-list.service';
import AppProductsGrid from '@/components/app/AppProductsGrid.vue';

export function buildOrderConfig(
  orderStatusOptions: SeverityItem[],
  mode: 'user-ro' | 'moder-ro',
): AppGridConfig<ModerOrderDisplayItem> {
  return {
    allowExpand: true,
    allowAdd: () => useRouter().push('/user/new-order'),
    slots: {
      expansion: x => {
        return [<AppProductsGrid data={x.data.products} mode={mode} />];
      },
    },
    labels: {
      products: '',
      createdAt: '',
      createdBy: '',
      orderPublicStatus: '',
      orderPrivateStatus: '',
      shippingMethod: '',
      shippingMethodId: '',
      id: '#',
      shippingMethodName: 'Получение',
      vatDocumentRequired: 'VAT',
      positions: 'Позиций',
      items: 'Единиц',
      productsPriceToPay: 'К оплате за товары',
      shippingPriceToPay: 'К оплате за доставку',
      orderStatusTypeId: 'Статус',
      totalPriceToPay: 'Всего к оплате',
      totalPriceNet: 'Всего цена нетто',
      totalPriceGross: 'Всего цена брутто',
      deliveryDate: 'Дата доставки',
      shippingDate: 'Дата выдачи',
      comment: 'Коментрарий пользователя',
      notes: 'Заментки для модератора',
    },
    columns: {
      products: false,
      createdAt: false,
      createdBy: false,
      orderPublicStatus: false,
      orderPrivateStatus: false,
      shippingMethod: false,
      shippingMethodId: false,
      id: routerLinkIdPreset('/user/my-order-info?orderId='),
      shippingMethodName: stringColumnPreset,
      vatDocumentRequired: boolColumnPreset,
      positions: stringColumnPreset,
      items: stringColumnPreset,
      productsPriceToPay: currencyColumnPresetNoEdit,
      shippingPriceToPay: currencyColumnPresetNoEdit,
      totalPriceToPay: currencyColumnPresetNoEdit,
      orderStatusTypeId: severityColumnPreset(orderStatusOptions),
      totalPriceNet: currencyColumnPresetNoEdit,
      totalPriceGross: currencyColumnPresetNoEdit,
      deliveryDate: stringColumnPreset,
      shippingDate: stringColumnPreset,
      comment: stringColumnPreset,
      notes: stringColumnPreset,
    },
  };
}
